body {
  font-size: 14px;
  line-height: 120%;
  font-weight: 400;
  margin: 0;
  padding: 0;
  position: relative;
  min-width: 320px;
  margin: 0 auto;
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #0c1016;
  color: #c5c5c5;
  overflow-x: hidden; }

body,
*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

.header {
  border-bottom: 1px solid #194fa4;
  margin-bottom: 30px; }
  @media (max-width: 499px) {
    .header {
      margin-bottom: 0; } }

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .nav h2 {
    margin: 0; }
    @media (max-width: 768px) {
      .nav h2 {
        font-size: 18px; } }
  @media (max-width: 499px) {
    .nav {
      flex-direction: column-reverse;
      gap: 20px; } }

.button,
button {
  text-decoration: none;
  display: block;
  cursor: pointer;
  color: inherit;
  text-align: center;
  font-size: 16px;
  line-height: inherit;
  font-family: "Open Sans", "Arial", sans-serif;
  padding: 10px 25px;
  background-color: #194fa4;
  border-radius: 8px;
  transition: 0.3s ease-in-out; }
  .button:hover,
  button:hover {
    background-color: #16448e; }
  .button:active,
  button:active {
    background-color: #123a78; }
  .button--secondary,
  button--secondary {
    background-color: #0f2f62; }
    .button--secondary:hover,
    button--secondary:hover {
      background-color: #0c244b; }
    .button--secondary:active,
    button--secondary:active {
      background-color: #081a35; }
  @media (max-width: 1024px) {
    .button,
    button {
      padding: 10px 20px; } }

.wrapper {
  max-width: 1240px;
  width: 100%;
  padding: 20px;
  margin: 0 auto; }

@font-face {
  font-family: "PT Sans";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: local("PT Sans"), local("PTSans-Regular"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff"); }

@font-face {
  font-family: "PT Sans";
  font-display: swap;
  font-weight: 700;
  font-style: normal;
  src: local("PT Sans Bold"), local("PTSans-Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff"); }

.movie {
  display: grid;
  grid-template-columns: 2fr 3fr 2fr;
  grid-template-areas: "poster description rates" "poster info starring";
  grid-gap: 40px; }
  @media (max-width: 768px) {
    .movie {
      grid-template-columns: 2fr 3fr;
      grid-template-areas: "poster rates" "poster description" "poster starring" "poster info"; } }
  @media (max-width: 499px) {
    .movie {
      grid-template-columns: 1fr;
      grid-template-areas: "poster" "rates" " description" " starring" " info";
      grid-gap: 20px; } }

.movie__img-wrap {
  grid-area: poster;
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding: 20px;
  border-radius: 0 20px 0 20px;
  border: 1px solid #194fa4; }
  @media (max-width: 768px) {
    .movie__img-wrap {
      padding: 10px;
      border: none; } }

.movie__poster img {
  width: 100%; }

.movie__buttons-wrap {
  display: flex;
  justify-content: center;
  gap: 20px; }
  @media (max-width: 1024px) {
    .movie__buttons-wrap {
      flex-direction: column; } }

.movie__description {
  grid-area: description;
  display: flex;
  flex-direction: column;
  gap: 30px; }
  @media (max-width: 499px) {
    .movie__description {
      gap: 10px; } }

.movie__title {
  font-size: 40px;
  line-height: 50px;
  margin: 0; }
  @media (max-width: 1024px) {
    .movie__title {
      font-size: 35px;
      line-height: 40px; } }
  @media (max-width: 768px) {
    .movie__title {
      font-size: 30px;
      line-height: 35px; } }

.movie__plot {
  margin: 0;
  font-size: 18px;
  line-height: 25px; }
  @media (max-width: 768px) {
    .movie__plot {
      font-size: 16px;
      line-height: 23px; } }

.movie__rates {
  grid-area: rates; }

.movie__rates-imbd {
  color: #6d9de9;
  font-size: 30px;
  line-height: 40px;
  margin: 0; }

.movie__about {
  grid-area: info;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 10px; }

.movie__about-item {
  display: flex; }

.marker {
  color: #838383;
  min-width: 90px;
  display: block; }

.movie__starring {
  grid-area: starring; }
  .movie__starring h3 {
    color: #6d9de9; }

.info {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 10px; }

.search {
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-bottom: 50px; }
  @media (max-width: 499px) {
    .search {
      gap: 20px;
      margin-bottom: 30px; } }

.search__title {
  text-align: center;
  font-size: 40px;
  margin: 0; }
  @media (max-width: 1024px) {
    .search__title {
      font-size: 30px; } }
  @media (max-width: 499px) {
    .search__title {
      font-size: 20px; } }

.search__input {
  width: 100%;
  padding: 20px;
  text-align: center;
  font-size: 20px;
  border-radius: 6px;
  transition: 0.3s ease-in-out;
  border: none;
  font-family: "Open Sans", "Arial", sans-serif; }
  .search__input:hover {
    background-color: #d8eaff; }
  .search__input:focus {
    outline: 2px solid #6d9de9;
    outline-offset: 4px; }

.list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px; }
  @media (max-width: 1024px) {
    .list {
      grid-template-columns: repeat(3, 1fr); } }
  @media (max-width: 768px) {
    .list {
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 20px; } }
  @media (max-width: 499px) {
    .list {
      grid-gap: 10px; } }

.card {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
  border: 1px solid #333333;
  border-radius: 6px; }
  .card a.button {
    margin-top: auto; }
  @media (max-width: 768px) {
    .card {
      padding: 10px;
      gap: 10px; } }

.card__poster {
  width: 100%;
  cursor: pointer; }
  .card__poster img {
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: center;
       object-position: center; }

.card__title {
  margin: 0;
  font-size: 18px;
  flex-grow: 1; }
  @media (max-width: 768px) {
    .card__title {
      font-size: 15px; } }
  @media (max-width: 499px) {
    .card__title {
      font-size: 12px; } }
